import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { IPricingPlanModel } from '../model/pricing-plan.model';
import { FleioApiService } from '../../fleio-api.service';
import { ConfigService } from '../../../config/config.service';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class PricingPlansApiService extends FleioApiService<IPricingPlanModel> {
  // noinspection JSUnusedGlobalSymbols
  constructor(protected httpClient: HttpClient, private config: ConfigService) {
    super(config.getPanelApiUrl('openstack/billing/plan'));
  }

  getAlternativePlans(currentPlanId: number): Observable<IPricingPlanModel[]> {
    return this.list().pipe(map(plans => {
      const alternativePlans = [];

      // TODO: do this query in backend
      for (const plan of plans.objects){
        if (plan.id !== currentPlanId) {
          alternativePlans.push(plan);
        }
      }

      return alternativePlans;
    }));
  }
}
